.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 20px;
}

@media (width >= 1024px) {
  .container {
    padding: 0 40px;
  }
}

.icon {
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  width: 100%;
  padding-bottom: 100%;
  text-decoration: none;
  display: block;
  position: relative;
}

.icon--phone {
  background-image: url("phone.d38c1760.svg");
}

.icon__tooltip {
  letter-spacing: 1px;
  color: #0078db;
  text-transform: uppercase;
  opacity: 0;
  pointer-events: none;
  width: max-content;
  padding-right: 18px;
  font-weight: 700;
  transition: opacity .3s;
  display: block;
  position: absolute;
  top: 50%;
  right: 100%;
  transform: translateY(-50%);
}

.icon:hover .icon__tooltip {
  opacity: 1;
  pointer-events: all;
}

.icon--menu {
  background-image: url("menu.751df960.svg");
}

.icon--menu:hover {
  background-image: url("menu-hover.193478dc.svg");
}

.icon--cross {
  background-image: url("cross.d4237fba.svg");
  grid-column: -2 / -1;
}

.logo__image {
  height: 18px;
  display: block;
}

@media (width >= 640px) {
  .logo__image {
    height: 24px;
  }
}

.grid {
  grid-template-columns: repeat(2, 1fr);
  column-gap: 20px;
  display: grid;
}

@media (width >= 640px) {
  .grid {
    grid-template-columns: repeat(8, 1fr);
    column-gap: 24px;
  }
}

@media (width >= 1024px) {
  .grid {
